.App-header {
  background-color: #282c34;
  min-height: 4vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 1.5vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
}

.content-container {
  height: 60vh;
  width: 95vw;
  display: flex;
}

.content-box {
  background-color: #282c34;
  color: white;
  width: 25%;
  border: 1px solid #ccc;
  padding: 20px;
  overflow-y: auto;
  justify-content: left;
}

.App-body {
  background-color: #282c34;
  min-height: 78vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.verse {
  position: relative;  /* Add this to make tooltip positioning work */
  font-size: calc(10px + 1vmin);
}

.verse:hover {
  background-color: #373d49;
}

.BookTitle {
  text-align: center;
}

.highlighted {
  background-color: #505764;
}